/* Main Default Layout. */

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    /* border: 2px solid black; */
    font-family: 'Gowun Dodum', sans-serif;
}

html {
    background-color: rgb(220, 220, 220);
}

a.active_class {
    border-bottom: 3.5px solid rgb(44, 44, 44);
    color: rgb(44, 44, 44);
    transform: scale(1.1);
}

/* Header Section. */

header {
    /* border: 2px solid black; */
    display: flex;
    padding: 20px;
    background-color: white;
    color: rgb(151, 151, 151);
    font-size: 0.7em;
    letter-spacing: 1px;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    /* width: auto; */
}

.links {
    height: 50px;
    display: flex;
    width: 130px;
    margin-left: 14%;
    justify-content: space-between;
    align-items: center;
}

.links a {
    text-decoration: none;
    color: rgb(151, 151, 151);
    font-size: 1.2em;
    border-bottom: 0px solid rgb(44, 44, 44);
    transition-duration: 70ms;
}

#link-1 {
    margin-left: 10px;
}

#link-2 {
    margin-left: 10px;
}

.links a:hover {
    border-bottom: 3.5px solid rgb(44, 44, 44);
    color: rgb(44, 44, 44);
    transform: scale(1.1);
}

.item-1 a {
    color: rgb(87, 87, 87);
    text-decoration: none;
}

.item-1 {
    display: flex;
    width: 67%;
    height: 55px;
    align-items: center;
}

.item-2 {
    display: flex;
    width: 30%;
    height: 55px;
    flex-direction: row;
    align-items: center;
    color: rgb(63, 63, 63);
    justify-content: flex-end;
    padding-right: 90px;
}

#tr-icon {
    display: none;
    position: relative;
    z-index: 5;
}

#tr-icon:hover {
    cursor: pointer;
}

.dropdown {
    position: relative;
    display: inline-block;
    z-index: 5;
}

.dropdown-content {
    display: none;
    background-color: #f9f9f9;
    width: 500px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    padding: 12px 16px;
    z-index: 1;
    text-align: center;
    margin-top: 157px;
}

.dropdown:hover .dropdown-content {
    display: block;
    position: relative;
    z-index: -1;
}

.dropdown-content a {
    text-decoration: none;
}

@media only screen and (max-width: 760px) {
    #tr-icon {
        display: block;
    }
    #time-btn {
        display: none;
    }
    .links {
        display: none;
    }
    .item-2 {
        padding-right: 10px;
    }
    .all-items {
        margin-left: 1em;
    }
}

/* Main Content Section. */

#main-content {
    display: flex;
    width: 100%;
}

footer {
    padding: 40px;
    background-color: white;
    color: rgb(151, 151, 151);
    text-align: center;
    letter-spacing: 1px;
    bottom: 0;
}

.all-items {
    display: flex;
    margin: 1em 4em;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
    /* border: 2px solid black; */
    width: 100%;
}

/* Card Design. */

.container {
    height: 449px;
    width: 405px;
    /* border: 2px solid black; */
}

.container-card {
    height: 345px;
    width: 300px;
    margin: 50px;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0px 10px 5px rgb(199, 199, 199);
    transition-duration: 0.2s;
}

.container-card:hover {
    transform: scale(1.05);
    box-shadow: 10px 12px 5px rgb(159, 159, 159);

}

.container-card img {
    border-radius: 10px 10px 0px 0px;
}

.container-card a {
    text-decoration: none;
}

.container-card a:hover {
    cursor: pointer;
}

.container-card h4 {
    text-transform: uppercase;
    font-size: 0.9em;
    letter-spacing: 2px;
    color: rgb(151, 151, 151);
}

.container-card h3 {
    color: black;
}

.content-card {
    margin: 5px 0px 10px 20px;
}

.content-card button {
    margin-top: 5%;
    padding: 2px 7px;
    text-transform: uppercase;
    font-size: 0.75em;
    letter-spacing: 1.5px;
    color: rgb(131, 131, 131);
    border: 2px solid rgb(131, 131, 131);
}

.content-card button:hover {
    cursor: pointer;
}

#title-ott {
    margin: 15px;
    text-align: center;
}

@media only screen and (max-width: 490px) {
    .all-items {
        margin: 1em 0em;
    }

    .container {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .container-card {
        height: max-content;
    }

    .content-card button {
        margin-bottom: 10px;
    }
}

/* Time button division. */

#time-btn {
    text-align: center;
    height: 50px;
    /* border: 2px solid black; */
}

#time-btn button {
    margin-top: 50%;
    padding: 10px 30px;
    width: 171px;
    height: 39px;
    color: white;
    border: 0px solid transparent;
    border-radius: 50px;
    background-color: blue;
    cursor: pointer;
    box-shadow: 3px 5px 5px rgb(167, 167, 167);
    transition-duration: 0.1s;
}

#time-btn button:hover {
    height: 45px;
    width: 180px;
    box-shadow: 3px 5px 5px rgb(85, 85, 85);
}